'use client'

import { useState } from 'react'

// ==========================================
// TYPES
// ==========================================

interface AISettings {
  enabled: boolean
  autoTriageEnabled: boolean
  autoResponseEnabled: boolean
  autoResolutionEnabled: boolean
  humanApprovalRequired: boolean
  maxAutoResolutionsPerDay: number
  preferredModel: string
  confidenceThreshold: number
  responseStyle: 'formal' | 'friendly' | 'professional'
  systemPrompt: string
}

interface Agent {
  id: string
  name: string
  email: string
  role: 'agent' | 'senior_agent' | 'team_lead' | 'manager' | 'admin'
  status: 'online' | 'away' | 'busy' | 'offline'
  skills: string[]
  maxTickets: number
  currentTickets: number
  avatar?: string
}

interface Team {
  id: string
  name: string
  description: string
  leadId?: string
  memberIds: string[]
  categories: string[]
  autoAssign: boolean
}

interface Automation {
  id: string
  name: string
  description: string
  trigger: string
  action: string
  enabled: boolean
  conditions: string[]
  executionCount: number
  lastExecuted?: string
}

// ==========================================
// MOCK DATA
// ==========================================

